import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { connect } from 'react-redux';
import { addToDo, fetchToDo } from './actions';

class App extends Component {
  constructor(props, context) {
    super(props, context);

    this.handleSubmitForm = this.handleSubmitForm.bind(this);
    this.handleTextChange = this.handleTextChange.bind(this);

    this.state = {
      nextToDoValue: ''
    };
  }

  componentWillMount() {
    this.props.dispatch(fetchToDo());
  }

  handleSubmitForm(event) {
    event.preventDefault();
    event.stopPropagation();
    this.props.dispatch(addToDo(this.state.nextToDoValue)) // dispatch an action to the store, and the action dispatches the function to all of the reducers (only one atm)
    this.setState(() => ({
      nextToDoValue: ''
    }));
  }

  handleTextChange(event) {
    const textValue = event.target.value;

    this.setState(() => ({
      nextToDoValue: textValue
    }));
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <form onSubmit={ this.handleSubmitForm }>
          <input type="text" onChange={ this.handleTextChange } value={ this.state.nextToDoValue } /> <button>Add</button>
        </form>
        <ul>
          {
            this.props.todos.map(value => <li>{value}</li>)
          }
        </ul>
      </div>
    );
  }
}

export default connect(state => { // connecting the redux provider to App, 'state' might be better as 'storeState' but is not standard convention
  return { todos: state.todos }; //putting entire store into props & adding 'dispatch' function, line22
  // the return is the selector
})(App);



