export function addToDo(text) {
    return {
        type: 'ADD_TODO',
        payload: {
            promise: async () => {
                let res = await fetch('/api/todo', {
                    body: JSON.stringify({
                        todo: text
                    }),
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    method: 'POST'
                });

                if (res.status !== 200) {
                    throw new Error(`server returned ${ res.status }`);
                }

                return (await res.json()).todos;
            }
        }
    }
}

export function fetchToDo() {
    // return {
    //     type: 'FETCH_TODO',
    //     payload: {
    //         promise: fetch('/api/todo')
    //             .then(res => {
    //                 if (res.status === 200) {
    //                     return res.json();
    //                 } else {
    //                     throw new Error(`server returned ${ res.status }`);
    //                 }
    //             })
    //             .then(json => json.todos)
    //     }
    // };

    return {
        type: 'FETCH_TODO',
        payload: {
            promise: async () => {
                const res = await fetch('/api/todo');

                if (res.status === 200) {
                    return (await res.json()).todos;
                } else {
                    throw new Error(`server returned ${ res.status }`);
                }
            }
        }
    };
}

