import { FULFILLED } from 'redux-promise-middleware';

let dummyToDo = [];

export default function(todos = dummyToDo, action) {
    console.log(action);

    switch(action.type) {
        case `ADD_TODO_${ FULFILLED }`:
        case `FETCH_TODO_${ FULFILLED }`:
            return action.payload;

        default:
            return todos;
    }
}


